import * as React from "react";
import Card from "@mui/material/Card";
import Button from "@mui/material/Button";
import Typography from "@mui/material/Typography";
import Image from "../img/cleaning2.jpg";
import InputLabel from "@mui/material/InputLabel";
import MenuItem from "@mui/material/MenuItem";
import FormHelperText from "@mui/material/FormHelperText";
import FormControl from "@mui/material/FormControl";
import Select from "@mui/material/Select";
import Container from "@mui/material/Container";
import TextField from "@mui/material/TextField";
import Box from "@mui/material/Box";
import Paper from "@mui/material/Paper";
import PropTypes from "prop-types";
import { styled } from "@mui/material/styles";
import SettingsIcon from "@mui/icons-material/Settings";
import Accordion from "@mui/material/Accordion";
import AccordionDetails from "@mui/material/AccordionDetails";
import AccordionSummary from "@mui/material/AccordionSummary";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import Grid from "@mui/material/Grid";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPaperclip } from "@fortawesome/free-solid-svg-icons";
import { faPhotoVideo } from "@fortawesome/free-solid-svg-icons";
import IconButton from "@mui/material/IconButton";
import Fab from "@mui/material/Fab";
import InputBase from "@mui/material/InputBase";
import Badge from "@mui/material/Badge";
import Chip from "@mui/material/Chip";
import Chart from "react-google-charts";
import Rating from "@mui/material/Rating";
import { withStyles } from "@material-ui/core/styles";

export default function CleaningPlan(props) {
  const [expanded, setExpanded] = React.useState(false);
  const handleDelete = () => {};
  const [value, setValue] = React.useState(2);

  const handleChange2 = (panel) => (event, isExpanded) => {
    setExpanded(isExpanded ? panel : false);
  };

  const Item = styled(Paper)(({ theme }) => ({
    padding: theme.spacing(1),
    textAlign: "center",
    color: theme.palette.text.secondary,
  }));

  const styles = (theme) => ({
    container: {
      display: "flex",
      flexWrap: "wrap",
    },
    textField: {
      marginLeft: theme.spacing.unit,
      marginRight: theme.spacing.unit,
      width: 200,
    },
  });

  const { classes } = props;

  return (
    <main>
      <Paper
        style={{
          padding: 20,
          margin: 20,
          fontFamily: "Oswald",
          border: "1px solid black",
        }}
      >
        <div className="req-header">Календарь плановой уборки</div>

        <Chart
          width={1000}
          height={200}
          chartLanguage={"ru"}
          chartType="Calendar"
          loader={<div>Loading Chart</div>}
          data={[
            [
              { type: "date", id: "Date" },
              { type: "number", id: "Won/Loss" },
            ],
            [new Date(2022, 2, 4), 0],
            [new Date(2022, 2, 5), 3],
            [new Date(2022, 2, 7), 1],
            [new Date(2022, 2, 8), 5],
            [new Date(2022, 2, 12), 5],
            [new Date(2022, 2, 13), 1],
            [new Date(2022, 2, 15), 1],
            [new Date(2022, 2, 16), -4],
            [new Date(2022, 1, 4), 10],
            [new Date(2022, 1, 5), 3],
            [new Date(2022, 1, 7), -1],
            [new Date(2022, 1, 8), 2],
            [new Date(2022, 1, 12), -1],
            [new Date(2022, 1, 13), 1],
            [new Date(2022, 1, 15), 1],
            [new Date(2022, 1, 16), 9],
          ]}
          options={{
            title: "",
          }}
          rootProps={{ "data-testid": "2" }}
        />

        <Grid container spacing={2}>
          <Grid
            item
            md={2}
            style={{ display: "flex", justifyContent: "center", width: "100%" }}
          >
              <TextField
                id="date"
                label="Дата уборки"
                type="date"
                defaultValue="2022-02-24"
                InputLabelProps={{
                  shrink: true,
                }}
              />
          </Grid>
          <Grid item md={2}>
              <Button id='makedontdo' size="small" color="primary" variant="contained">
                Уборка не проводилась
              </Button>
          </Grid>
          <Grid item md={2}>
            <Item>
              <div>
                <Box sx={{ "& > legend": { mt: 2 } }}>
                  <Typography component="legend">
                    Оценка качества уборки
                  </Typography>
                  <Rating
                    name="simple-controlled"
                    value={value}
                    onChange={(event, newValue) => {
                      setValue(newValue);
                    }}
                  />
                </Box>
              </div>
            </Item>
          </Grid>
          <Grid item md={4}>
              <Button id='signbtn' size="small" color="secondary" variant="contained">
                Подписать
              </Button>
          </Grid>
        </Grid>
      </Paper>
    </main>
  );
}
