import * as React from "react";
import { styled } from "@mui/material/styles";
import Typography from "@mui/material/Typography";
import Box from "@mui/material/Box";
import Button from "@mui/material/Button";
import Grid from "@mui/material/Grid";
import Paper from "@mui/material/Paper";
import { makeStyles } from "@material-ui/styles";
import { fontSize } from "@mui/system";

const FooterContainer = styled(Paper)(() => ({
  textAlign: "center",
  position: "absolute",
  bottom: "0",
  color: "red",
  width: "100%",
  height: "70",
  background: "rgb(255, 209, 13)",
}));

const Item = styled(Paper)(({ theme }) => ({
  padding: theme.spacing(1),
  textAlign: "center",
  color: theme.palette.text.secondary,
}));

export default function Footer() {
  return (
       <footer className="footer footer">
        <Grid container spacing={2} id="foolbar">
          <Grid
            item
            xs={8}
            style={{ display: "flex", justifyContent: "center", width:"100%"}}
          >
            <Item 
              style={{  boxShadow: "", paddingTop:0, backgroundColor: "rgb(255, 209, 13)" }}
              className="C"
              Id="phrase-footer"
            >
              <Typography variant="body2" fontSize="1em" fontFamily="Oswald">
                Помогаем экономить на уборке
              </Typography>
              <Typography variant="body2" fontFamily="Oswald">
                v.1.0.6
              </Typography>
            </Item>
          </Grid>
          <Grid
            item
            xs={4}
            style={{ display: "flex", justifyContent: "center", }}
          >
            <Item
              style={{
                boxShadow: "", paddingTop:0,
                width:"100%" , backgroundColor: "rgb(255, 209, 13)" 
              }}
            >
              <Button size="small" color="secondary" variant="contained">
                Заказать демонстрацию / Задать вопрос
              </Button>
            </Item>
          </Grid>
        </Grid>
      </footer>
  );
}
